            >
          </el-form>
        </div>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="24">
        <div class="bottom_top"></div>
        <div class="bottom_bottom">
          <div class="bottom_bottom_tittle">
            <span class="row_text">序号</span>
            <span class="row_text">设备编号</span>
            <span class="row_text">设备型号</span>
            <span class="row_text">详细地址</span>
            <span class="row_text">运营状态</span>
            <span class="row_text">设备状态</span>
