            >
              <span class="row_text">{{
                index + 1 + (dataS.pageIndex - 1) * 10
              }}</span>
              <span class="row_text">{{ item.innerCode }}</span>
              <span class="row_text">{{
                item.type ? item.type.name : "这TM没数据,这后端真坑人"
              }}</span>
              <span class="row_text">{{
                item.node.addr.replace(/.*-/, "")
              }}</span>
              <span class="row_text">{{
                item.vmStatus === 0
                  ? "未投放"
                  : item.vmStatus === 1
                  ? "运营"
                  : item.vmStatus === 3
                  ? "撤机"
                  : ""
              }}</span>
              <span class="row_text">
                <span class="row_text_state_G">{{
                  item.status["10001"] ? "在线" : "离线"
                }}</span>
                <span class="row_text_state_On">{{
                  item.status["10002"] ? "货道" : "离线"
                }}</span>
                <span class="row_text_state_State">{{
                  item.status["10003"] ? "转动轴" : "离线"
                }}</span>
              </span>
              <span class="row_text right">
                <a href="javascript:;" @click="getDetails(item.innerCode)"
