<script>
import {
  getManage,
  getSearch,
  getDetailSupply,
  getDetailRepair,
  getDetailSku,
  getDetailOrderAmount,
  getDetailOrderCount
} from '@/api/vm'
export default {
  data () {
    return {
      form: {
        id: ''
      },
      checkAll: false,
      innerCodeList: [],
      cities: [],
      isIndeterminate: false,
      dataS: {
        pageIndex: '0',
        pageSize: '',
        totalCount: '',
        totalPage: ''
      },
      loading: false,
      // ------弹出层新增
      vmType: [],
      nodeID: [],
      showBox: false,
      goodsAdd: {
        createUserId: 1,
        nodeId: '',
        vmType: ''
      },
      goodsRule: {
        vmType: [{ required: true, message: '请选择' }],
        nodeId: [{ required: true, message: '请选择' }]
      },
      formLabelWidth: '110px',
      // 弹出层操作,
      detailShow: false,
      detailData: {
        supply: '',
        repair: '',
        sku: [],
        orderAmount: '',
        orderCount: ''
      },
      detailLoading: false
    }
  },
  methods: {
    handleCheckAllChange (val) {
      if (val) {
        this.cities.forEach((item) => {
          if (!this.innerCodeList.includes(item.innerCode)) {
            // 如果选中的item不在列表中,则加入组里
            this.innerCodeList.push(item.innerCode)
          }
        })
      }
      this.innerCodeList = val ? this.innerCodeList : [] // 将选项加入已选择数组
      this.isIndeterminate = false // 将非全选状态设定为 -
    },
    handleCheckedCitiesChange (value) {
      const checkedCount = value.length // 每次改变传入数组长度
      this.checkAll = checkedCount === this.cities.length // 如果长度等于数组长度则全选
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.cities.length // 长度大于一且小于数组长度状态为-
    },
    async getDateS (id) {
      this.loading = true
      let res = []
      if (id) {
        const data = {
          pageIndex: 1,
          pageSize: 10,
          innerCode: id
        }
        res = await getSearch(data) // 搜索
      } else {
        res = await getManage(this.dataS) // 上一页下一页
      }
      this.cities = res.currentPageRecords
      this.dataS.pageIndex = res.pageIndex
      this.dataS.pageSize = res.pageSize
      this.dataS.totalPage = res.totalPage
      this.dataS.totalCount = res.totalCount
      this.innerCodeList = []
      this.isIndeterminate = false
      this.checkAll = false
      this.loading = false
    },
    async getDetails (id) {
      this.detailLoading = true
      const date = new Date()
      const year = date.getFullYear()
      const mouth = date.getMonth() + 1
      const day = date.getDate()
      // console.log(year, mouth, day)
      this.detailShow = true
      const dataNoTime = {
        innerCode: id,
        start: year + '-' + mouth + '-' + '01',
        end: year + '-' + mouth + '-' + (day > 9 ? day : '0' + String(day))
      }
      const dataTime = {
        start: year + '-' + mouth + '-' + '01' + ' 00:00:00',
        end:
          year +
          '-' +
          mouth +
          '-' +
          (day > 9 ? day : '0' + String(day)) +
          ' 23:59:59',
        innerCode: id
      }
      const res1 = await getDetailSupply(dataNoTime) // 维修
      // console.log('维修', res1)
      this.detailData.supply = res1
      const res2 = await getDetailRepair(dataNoTime)
      // console.log('供货', res2)
      this.detailData.repair = res2
      const res3 = await getDetailSku(dataNoTime)
      // console.log('商品详情', res3)
      this.detailData.sku = res3
      const res4 = await getDetailOrderAmount(dataTime)
      // console.log('销售额', res4)
      this.detailData.orderAmount = res4
      const res5 = await getDetailOrderCount(dataTime)
      // console.log('销售量', res5)
      this.detailData.orderCount = res5
      this.detailLoading = false
    }
  },
  async created () {
    await this.getDateS()
  }
}
</script>

